import styles from "./NormalCalendarEvent.module.scss";
import patterns from "./Patterns.module.scss";
import { EventContentArg } from "@fullcalendar/react";

/* import { Check, ErrorCircle } from "@myloc/myloc-gui"; */

import classNames from "classnames";

const PATTERNS = {
  BACKDIAGONAL: "backdiagonal",
  BUBBLES: "bubbles",
  CROSSES: "crosses",
  DOTS: "dots",
  GRID: "grid",
  HORIZONTALZIGZAG: "horizontalzigzag",
  HORIZONTAL: "horizontal",
  RHOMBS: "rhombs",
  VERTICALZIGZAG: "verticalzigzag",
  VERTICAL: "vertical",
} as const;

const ICONS = {
  RED_WARNING: "redWarning",
  DELIVERED: "delivered",
  YELLOW_CHECK: "yellowCheck",
  GREEN_CHECK: "greenCheck",
} as const;

const NormalCalendarEvent = ({ arg }: { arg: EventContentArg }) => {
  const event = arg.event;
  const eventExtend = event.extendedProps;

  const pattern = (() => {
    switch (eventExtend.pattern) {
      case PATTERNS.BACKDIAGONAL:
        return patterns.backdiagonal;
      case PATTERNS.BUBBLES:
        return patterns.bubbles;
      case PATTERNS.CROSSES:
        return patterns.crosses;
      case PATTERNS.DOTS:
        return patterns.dots;
      case PATTERNS.GRID:
        return patterns.grid;
      case PATTERNS.HORIZONTALZIGZAG:
        return patterns.horizontalzigzag;
      case PATTERNS.HORIZONTAL:
        return patterns.horizontal;
      case PATTERNS.RHOMBS:
        return patterns.rhombs;
      case PATTERNS.VERTICALZIGZAG:
        return patterns.verticalzigzag;
      case PATTERNS.VERTICAL:
        return patterns.vertical;

      default:
        return null;
    }
  })();

  const icon = (() => {
    switch (eventExtend.icon) {
      case ICONS.RED_WARNING:
        return <div></div> // <ErrorCircle color="red" /* customCssClass={iconStyles.redWarning}  */ size={"18px"} />;
      case ICONS.DELIVERED:
        return <div></div> /* <Check />; */
      case ICONS.YELLOW_CHECK:
        return <div></div> /* <Check color="yellow" />; */
      case ICONS.GREEN_CHECK:
        return <div></div> /* <Check color="green" />; */

      default:
        return null;
    }
  })();
  return (
    <div className={classNames(styles.calender__event, pattern)}>
      {/* Header && Icon  event.extendedProps.icon*/}
      <div
        className={styles.icon__text}
        style={{
          color: "#000",
          backgroundColor: event.extendedProps.headerColor,
        }}
      >
        {icon}
        <div>
          {arg.timeText} {event.title}
        </div>
      </div>

      {/* Body */}
      <div
        style={{
          color: `${eventExtend.textColor === "" ? "#000" : eventExtend.textColor}`,
        }}
      >
        {eventExtend.bodyText}
      </div>
    </div>
  );
};

export default NormalCalendarEvent;
