import styles from "./Url.module.scss";
import { Slides } from "../../dataTypes";

const Url = ({ slide }: { slide: Slides }) => {
  return (
    <div className={styles.website}>
      <iframe
        title={slide.externalUrl}
        className={styles.website}
        src={slide.externalUrl}
        scrolling="no"
        frameBorder="0"
      />
    </div>
  );
};

export default Url;
