import PropTypes from "prop-types";

import FullCalendar from "@fullcalendar/react";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import svLocale from "@fullcalendar/core/locales/sv";

import SmallCalendarEvent from "./CalenderSlideshow/SmallCalendarEvent";
import NormalCalendarEvent from "./CalenderSlideshow/NormalCalendarEvent";
import { CalenderEventsCalendar, Slides } from "../../dataTypes";

import differenceInMinutes from "date-fns/differenceInMinutes";

const TIME_OPTIONS = {
  CALENDER_TYPE_INTERVAL: 90,
  SLOT_DURATION_INTERVAL: 9 * 60,
  SLOT_DURATION_LONG_DAY: "02:00",
  SLOT_DURATION_SHORT_DAY: "00:30",
} as const;

const Calendar = ({
  height,
  headerHeight,
  workDayStart,
  workDayEnd,
  slide,
  calendarEvent,
}: {
  height: string;
  headerHeight: string;
  workDayStart: string;
  workDayEnd: string;
  slide: Slides;
  calendarEvent: CalenderEventsCalendar[];
}) => {
  const timeDuration = (startDate: Date | string, endDate: Date | string) => {
    if (typeof startDate === "string") startDate = new Date(`1995-12-17T${startDate}`);
    if (typeof endDate === "string") endDate = new Date(`1995-12-17T${endDate}`);
    return differenceInMinutes(endDate, startDate);
  };

  return (
    <div>
      <FullCalendar
        plugins={[timeGridPlugin, dayGridPlugin]}
        initialView={slide.viewMode}
        weekends={slide.showWeekends}
        initialDate={slide.startDate}
        locale={svLocale}
        headerToolbar={false}
        firstDay={1}
        slotMinTime={workDayStart}
        slotMaxTime={workDayEnd}
        height={window.innerHeight > 600 ? `calc(${height} - ${headerHeight})` : height}
        expandRows={true}
        nowIndicator={true}
        slotDuration={
          timeDuration(workDayStart, workDayEnd) > TIME_OPTIONS.SLOT_DURATION_INTERVAL //
            ? TIME_OPTIONS.SLOT_DURATION_LONG_DAY
            : TIME_OPTIONS.SLOT_DURATION_SHORT_DAY
        }
        events={calendarEvent}
        eventContent={function (arg) {
          return (
            <>
              {arg.event.start && arg.event.end ? (
                timeDuration(arg.event.start, arg.event.end) > TIME_OPTIONS.CALENDER_TYPE_INTERVAL ? (
                  <NormalCalendarEvent arg={arg} />
                ) : (
                  <SmallCalendarEvent arg={arg} />
                )
              ) : (
                <SmallCalendarEvent arg={arg} />
              )}
            </>
          );
        }}
      />
    </div>
  );
};

Calendar.propTypes = {
  height: PropTypes.string.isRequired,
  headerHeight: PropTypes.string.isRequired,
  workDayStart: PropTypes.string.isRequired,
  workDayEnd: PropTypes.string.isRequired,
};

export default Calendar;
