
import Calendar from "./TypesSlideshow/Calendar";
import Url from "./TypesSlideshow/Url";
import File from "./TypesSlideshow/File";
import styles from "./PanelSlideshow.module.scss";
import { Slides, CalenderEventsCalendar } from "../dataTypes";

import { useCallback, useEffect, useState } from "react";

const SLIDESHOW_TYPES = {
  EXTERNAL_PAGE: "ExternalPage",
  IMAGE: "Image",
  CALENDER: "calendar",
} as const;

const PanelSlideshow = ({
  height,
  headerHeight,
  slides,
  tempWorkDayTime,
  calendarEvents,
}: {
  height: string;
  headerHeight: string;
  slides: Slides[];
  tempWorkDayTime: string[];
  calendarEvents: CalenderEventsCalendar[];
}) => {
  const [current, setCurrent] = useState(0);
  const length = slides.length;

  const nextSlide = useCallback(() => {
    setCurrent(current === length - 1 ? 0 : current + 1);
  }, [current, length]);

  useEffect(() => {
    const currentSlideTime = slides[current].displayTime;
    const intervals = setInterval(nextSlide, 1000 * currentSlideTime);

    return () => clearInterval(intervals);
  }, [slides, current, nextSlide]);

  return (
    <div
      className={styles.slideshow__window}
      style={{ height: window.innerHeight > 600 ? `calc(${height} - ${headerHeight})` : height }}
    >
      {slides
        .sort((a: Slides, b: Slides) => a.sequence - b.sequence)
        .map((slide, index) => {
          return (
            <div className={index === current ? styles.slide__img__active : styles.slide__img} key={index}>
              {(() => {
                switch (slide.type) {
                  case SLIDESHOW_TYPES.EXTERNAL_PAGE:
                    return <Url slide={slide} />;

                  case SLIDESHOW_TYPES.IMAGE:
                    return <File file={slide.file} />;

                  case SLIDESHOW_TYPES.CALENDER:
                    return (
                      <Calendar
                        height={height}
                        headerHeight={headerHeight}
                        workDayStart={tempWorkDayTime[0]}
                        workDayEnd={tempWorkDayTime[1]}
                        slide={slide}
                        calendarEvent={calendarEvents.filter((event: CalenderEventsCalendar) =>
                          slide.calendarEvents?.ids.includes(event.id),
                        )}
                      />
                    );

                  default:
                    return null;
                }
              })()}
            </div>
          );
        })}
    </div>
  );
};



export default PanelSlideshow;
