import PropTypes from "prop-types";
import styles from "./HeaderSlideshow.module.scss";

const HeaderSlideshow = ({
  projectName,
  projectID,
  companyLogo,
  headerHeight,
}: {
  projectName: string;
  projectID: string;
  companyLogo: string;
  headerHeight: string;
}) => {
  return (
    <div className={styles.header__slideshow} style={{ height: `${headerHeight}` }}>
      <img
        className={styles.header__myloc}
        src="https://myloc.se/wp-content/uploads/2016/11/mylocconstransp..png"
        alt="MyLoc"
      />

      <div className={styles.header__text}>
        {projectID} - {projectName}
      </div>

      <div className={styles.header__kund}>
        <img className={styles.companyLogo} src={companyLogo} alt={projectName} />
      </div>
    </div>
  );
};

HeaderSlideshow.propTypes = {
  projectName: PropTypes.string.isRequired,
  projectID: PropTypes.string.isRequired,
  companyLogo: PropTypes.string.isRequired,
  headerHeight: PropTypes.string.isRequired,
};

export default HeaderSlideshow;
