  const validateSlideshowData = (slideshowData: Partial<SlideshowType>): boolean => {
    if (
      !slideshowData.slideshow ||
      !slideshowData.calendarEvents ||
      !slideshowData.slideshow.workDayEnd ||
      !slideshowData.slideshow.workDayEnd ||
      !slideshowData.slideshow.workDayStart ||
      !slideshowData.slideshow.companyLogoUrl ||
      !slideshowData.slideshow.projectId ||
      !slideshowData.slideshow.projectName
    )
      return false;

    return true;
  };

  const [slideshowInfo, setSlideshowInfo] = useState<SlideshowType>(() => {
    const getSlideshowData = window.localStorage.getItem("slides");

    if (getSlideshowData === null || !validateSlideshowData(slideshowSmartTvTest as SlideshowType)) {
      return slideshowSmartTvTest as SlideshowType;
    } else {
      return slideshowSmartTvTest as SlideshowType;
    }
  });

  

